header {
    width: 100%;
    height: 10vh;
    margin: 0 auto;
    display: flex;
    position: relative;
}
.headerContainer{
    padding: 0 3%;
    width: 100%;
    display: flex;
    align-items:center;
}
.header_wrapper{
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* logo start */

.logoWrapper {
    width: 144px;
    height: 31px;
}
.logoContainer {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: space-around;
}
.vector_1 {
    background-image: url('../../../images/Vector1.svg');
    background-repeat: no-repeat;
    background-size: contain;
    width: 24px;
    height: 24px;
}
.vector_2 {
    background-image: url('../../../images/Vector2.svg');
    background-repeat: no-repeat;
    background-size: contain;
    width: 34px;
    height: 31px;
}

.vector_3 {
    background-image: url('../../../images/Vector3.svg');
    background-repeat: no-repeat;
    background-size: contain;
    width: 12px;
    height: 23px;
}

.vector_4 {
    background-image: url('../../../images/Vector4.svg');
    background-repeat: no-repeat;
    background-size: contain;
    width: 24px;
    height: 24px;
}

.vector_5 {
    background-image: url('../../../images/Vector5.svg');
    background-repeat: no-repeat;
    background-size: contain;
    width: 38px;
    height: 24px;
}

/* logo end */

/* navbar start */
.navWrapper {
    width: 65%;
    height: 24px;
}

.list_table {
    display: flex;
}
.list_table li {
    list-style: none;
    margin-left: 5%;
}
.list_table li a {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-decoration: none;
    color: #292929;
}
/* navbar enc */

/* login start */

.loginWrapper {
    width: 16%;
    height: 44px;
    display: flex;
    justify-content: space-between;
}

.login {
    width: 25%;
    height: 100%;
    display: flex;
    align-items: center;
}
.loginBtn {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    border: none;
    color: #800080;
    background-color: white;
    cursor: pointer;
}

.registration {
    width: 65%;
    height: 100%;
}

.registrationBtn {
    background: #800080;
    border-radius: 32px;
    width: 100%;
    height: 100%;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;
    cursor: pointer;
    border: none;
}

/* login end */
/* icosn start */
.btnIcon{
    width: 41px;
    height: 33px;
    margin: auto 0;
    cursor: pointer;
    border: none;
    background: none;
}
.icon{
    border: 1px solid black;
    font-size: 36px;
    border: none;
}

/* icosn end */